.dishCard {
    border-radius: 1rem;
    overflow: hidden;
    box-shadow: 0.625rem 0.125rem 0.5rem rgba(0,0,0,0.1);
    width: 15.625rem;
    height: 15.625rem;
    font-family: Arial, sans-serif;
    padding-bottom: 2.5rem;
    background-color: white;
    position: relative;
}

.dishCard img {
    width: 15.625rem;
    height: 15.625rem;
    object-fit: cover;
    display: block;
}

.dishCardInfo {
    padding: 0.625rem;
    background-color: #fff;
    border-top: 1px solid #eee;
    text-align: center;
    height: 3.75rem;
}

.dishCardInfo h3 {
    margin: 0 0 0.3rem;
    font-size: 1rem;
}

.dishCardInfo p {
    position: absolute;
    left: 0.625rem;
    bottom: 0;
    font-weight: bold;
    color: #888;
}

.dishCardInfo p.Add {
    position: absolute;
    right: 0.625rem;
    bottom: 0;
    border: 1px solid black;
    font-weight: bold;
    color: #888;
}